import React, {useState} from 'react';

function Tabmenu4(props) {
    const [activeTab, setActiveTab] = useState('Home');
    const tabs = [
        {title:'Home', content:'메인화면'},
        {title:'About', content:'소개'},
        {title:'Contact', content:'문의하기'},
    ]

    const activeTabContent = tabs.find((tab)=>tab.title===activeTab)?.content;

    return (
        <div>
            <h3>TabMenu - 최적화</h3>
            <ul className='menu_list'>
                {tabs.map((tab)=> (
                <li
                    className={activeTab===tab.title?'act':''}
                    key={tab.title}
                    onClick={()=> setActiveTab(tab.title)}>
                        {tab.title}
                    </li>
                ))}
            </ul>
            <div className='tab_content'>
                {activeTabContent}
            </div>
        </div>
    );
}

export default Tabmenu4;