import React, {useState, useEffect} from 'react';
import data from './data/book_store.json';

function Bookstore(props) {
    const [books, setBooks] = useState([]);

    useEffect(()=>{ //비동기로 불러오기
        fetch(`${process.env.PUBLIC_URL}/data/book_store.json`)
        .then(response=> response.json())
        .then(json => setBooks(json))
        .catch(err=>console.error('데이터 불러오기 실패:', err));
    }, []);

    return (
        <>
            <h4>Bookstore json데이터 출력하기</h4>
            <p>1. 파일이 작은 경우 import방식으로 데이터 출력(정적)</p>
            <ul>
                {data.map(book=>(
                    <li key={book.num}>
                        Num : {book.num}&nbsp;
                        NAME : {book.name}&nbsp;
                        지역1 : {book.area1}&nbsp;
                        지역2 : {book.area2}&nbsp;
                        지역3 : {book.area3}&nbsp;
                        주문수량 : {book.BOOK_CNT}&nbsp;
                        주문자 : {book.owner_nm}&nbsp;
                        연락처 : {book.tel_num}&nbsp;
                    </li>
                ))}
            </ul>

            <p>2. 파일이 큰 경우 fetch로 비동기 방식으로 데이터 로딩하기</p>
            <ul>
                {books.map(book=> (
                    <li key={book.num}>
                    Num : {book.num}&nbsp;
                    NAME : {book.name}&nbsp;
                    지역1 : {book.area1}&nbsp;
                    지역2 : {book.area2}&nbsp;
                    지역3 : {book.area3}&nbsp;
                    주문수량 : {book.BOOK_CNT}&nbsp;
                    주문자 : {book.owner_nm}&nbsp;
                    연락처 : {book.tel_num}&nbsp;
                </li>
                ))}
            </ul>
        </>
    );
}

export default Bookstore;