import React from 'react';
import Dummy from '../data/product.json';
import './css/product.css';

function Yellow(props) {
    return (
        <article className='product'>
            <h3>빠른 예약, 화끈한 할인 '얼리썸머페스타'</h3>
            <ul className='product_list'>
                {Dummy.products.map(product=>(<li key={product.id}>
                    <img src={`${process.env.PUBLIC_URL}/images/${product.filename}`} alt="" />
                    <p>
                        <span className='product_title'>{product.title}</span><br />
                        <span className='price'>{product.price}</span>
                    </p>
                </li>))}
            </ul>
        </article>
    );
}

export default Yellow;