import React from 'react';
import data from './data/fruit.json';

function Fruit(props) {
    return (
        <>
            <h4>Fruit json데이터 출력하기</h4>
            <ul>
                {data.map(fruit=>(
                    <li key={fruit.num}>
                        Num : {fruit.num}&nbsp;
                        Name : {fruit.name}&nbsp;
                        Price : {fruit.price}&nbsp;
                        Color : {fruit.color}&nbsp;
                        Country : {fruit.country}&nbsp;
                    </li>
                ))}
            </ul>
        </>
    );
}

export default Fruit;