import React, {useState} from 'react';

function Usestate(props) {
    const style = {
        width: '300px',
        padding: '10px',
        margin: '10px',
        border: '3px solid #f00'
    }

    const button = {
        padding: '5px 20px',
        border: 'none',
        margin: '0px 5px 0px 0px',
        borderRadius: '5px'
    }

    const [num, setNum] = useState(1);
    const price = 42750;
    const total = price*num;

    const onDecrease = () => {
        if(num===1) {
            setNum(1);
        } else {
            setNum(num-1);
        }
    }

    const onIncrease = () => {
        setNum(num+1);
    }

    return (
        <>
            <h3>useState복습하기</h3>
            <p>클래스형 컴포넌트는 state값을 바로 사용가능 했습니다. 하지만 함수형 컴포넌트에서는 state값을 사용할 수 없었는데, 리액트 버전 16.8이상부터는 useState()함수가 추가되어 함수형 컴포넌트에서도 state값을 사용할 수 있게 되었습니다</p>
            <p>state는 자신 컴포넌트가 '유동적으로 변경될 값'을 관리할 때 사용합니다</p>
            <p>useState()함수 사용시 import React, useState 함수를 추가해야 한다</p>

            <pre>
                const [값변수, 값변경을 위한 함수] = useState(기본값); <br />
                const 값변경을 위한 함수 = (); //내용 <br />
                const 스테이트 값을 갖고 오는 변수 = useState(기본값); <br />
                const 스테이트 값 변수 = 스테이트 갖고 오는 변수[0]; <br />
                const 변경될 스테이트값 변수 = 스테이트 갖고 오는 변수 [1]; <br />
            </pre>

            <div style={style}>
                <p>값 변화 : {num}</p>
                <input type='button' value='-' onClick={onDecrease} style={button}/>
                <input type='button' value='+' onClick={onIncrease} style={button}/>
                <p>상품가격 : 7,300</p>
                <p>주문가격: {total}원</p>
            </div>
        </>
    );
}

export default Usestate;