import React, { Component } from 'react';
// import Swiper core and required modules
import { Navigation, Pagination, Scrollbar, Autoplay } from 'swiper/modules';

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

class Kullyp extends Component {
    render() {
        return (
            <>
                <main>
                    <article>
                        <h2>이미지 슬라이드</h2>
                        <Swiper
                            // install Swiper modules
                            modules={[Navigation, Pagination, Scrollbar, Autoplay]}
                            spaceBetween={0}
                            slidesPerView={1}
                            navigation
                            pagination={{ clickable: true }}
                            scrollbar={{ draggable: true }}
                            onSwiper={(swiper) => console.log(swiper)}
                            onSlideChange={() => console.log('slide change')}
                            autoplay ={{delay:3000}}
                        >
                            <SwiperSlide><img src={`${process.env.PUBLIC_URL}/images/banner1.jpg`}/></SwiperSlide>
                            <SwiperSlide><img src={`${process.env.PUBLIC_URL}/images/banner2.png`}/></SwiperSlide>
                            <SwiperSlide><img src={`${process.env.PUBLIC_URL}/images/banner3.png`}/></SwiperSlide>
                            <SwiperSlide><img src={`${process.env.PUBLIC_URL}/images/banner4.png`}/></SwiperSlide>
                        </Swiper>
                    </article>
                </main>
            </>
        );
    }
}

export default Kullyp;