import React from 'react';

function Student({student, onRemove}) {
    const flex = {display: 'flex'}
    return (
        <>
            <div style={flex}>
                <strong>이름 : {student.name}</strong>
                <strong>나이 : {student.age}</strong>
                <strong>이메일 : {student.email}</strong>
                <button onClick={()=>onRemove(student.id)}>데이터 삭제</button>
            </div>
            <hr />
        </>
    );
}

export default Student;