import React from 'react';
import data from './data/noodle.json';

function Noodle(props) {
    return (
        <>
            <h4>Noodle json데이터 출력하기</h4>
            <ul>
                {data.map(noodle=>(
                    <li key={noodle.num}>
                        Num : {noodle.num}&nbsp;
                        Name : {noodle.name}&nbsp;
                        Company : {noodle.company}&nbsp;
                        Kind : {noodle.kind}&nbsp;
                        Price : {noodle.price}&nbsp;
                        End Date : {noodle.e_date}&nbsp;
                    </li>
                ))}
            </ul>
        </>
    );
}

export default Noodle;