import React, {useState} from 'react';

function Form2(props) {
    let header ='';
    const [name, setName] = useState('');
    const [pw_name, setPassword] = useState('');
    const nameChange = (e) => {
        setName(e.target.value);
    }
    const pwChange = (e) => {
        setPassword(e.target.value);
    }

    if (name) {
        header = <p>사용자가 입력한 이름은 {name}입니다. <br />입력한 비밀번호는 {pw_name}입니다.</p>
    } else {
        header = '';
    }

    const Reset = (e) => {
        // alert('reset');
        setName('');
        setPassword('');
    }

    return (
        <>
            <h4>2. if문으로 조건식 달성하기</h4>
            {header}
            <form>
                <label for='name'>ID: </label>
                <input type='text' name='name' id='id_name' value={name} onChange={nameChange} />
                <br />
                <label for='password'>PASSWORD</label>
                <input type='text' name='password' id='pw_name' value={pw_name} onChange={pwChange} />
                <br />
                <input type='button' value="초기화" onClick={Reset} />
            </form>
        </>
    );
}

export default Form2;