import React from 'react';
import data from './data/orders.json';

function Orders(props) {
    return (
        <>
            <h4>Orders json데이터 출력하기</h4>
            <ul>
                {data.map(order=>(
                    <li key={order.num}>
                        Order Day : {order.o_day}&nbsp;
                        Customer Name : {order.c_name}&nbsp;
                        Goods' Code : {order.g_code}&nbsp;
                        Order Number : {order.o_num}&nbsp;
                    </li>
                ))}
            </ul>
        </>
    );
}

export default Orders;