import React, {useState} from 'react';
import './css/tabmenu.css';

const Tabmenu3 = () => {
    const [activeTab, setActiveTab] = useState('Home');
    const tabs = [
        {title:'Home', content:'메인화면'},
        {title:'About', content:'소개'},
        {title:'Contact', content:'문의하기'},
    ]
    return (
        <div>
            <h3>TabMenu - 배열객체를 생성하여 map함수로 컨텐츠 출력하기</h3>
            <ul className='menu_list'>
                {tabs.map((tab)=> (
                <li 
                className={activeTab===tab.title?'act':''}
                    key={tab.title}
                    onClick={()=> setActiveTab(tab.title)}>
                        {tab.title}
                    </li>
                ))}
            </ul>
            <div className='tab_content'>
                {activeTab==='Home'?tabs[0].content:null}
                {activeTab==='About'?tabs[1].content:null}
                {activeTab==='Contact'?tabs[2].content:null}
            </div>
        </div>
    );
}

export default Tabmenu3;