import React, {use, useState} from 'react';

function Form4(props) {
    // const [name, setName] = useState('');
    // const [id_text, setId] = useState('');
    const [formData, setFormData] = useState({
        name: '',
        id_text : ''
    });

    // const nameChange = (e) => {
    //     setName(e.target.value);
    // }

    // const idChange = (e) => {
    //     setId(e.target.value);
    // }

    const nameChange = (e) => {
        const {name, value} = e.target;
        setFormData((prev)=>({ //es6 스프레드 연산자
            ...prev, 
            [name] : value
        }))
    }

    // const onDelete = (e) => {
    //     setName('');
    //     setId('');
    // }

    const onDelete = () => {
        setFormData({
            name: '',
            id_text : ''
        });
    }

    return (
        <>
            <h4>4. 여러개의 input태그의 데이터 관리하기</h4>
            <ul>
                <li>e.target.value : 값에 접근하기</li>
                <li>e.target.name : name속성값에 접근하기</li>
                <li>this.setState({`[변수명]:값`}) : 속성명에 변수값을 사용하려면 대문자로 표시해야 함</li>
                <li>nameChange :값을 입력하였을 때 발생되는 이벤트 함수</li>
                <li>useState함수를 사용하여 상태관리값을 한꺼번에 선언하고 관리 할 수 있다</li>
            </ul>

            <form>
                <p>출력내용(이름) : </p>
                <input type='text' id='name' name='name' value={formData.name} onChange={nameChange} />

                <p>출력내용(아이디) : </p>
                <input type='text' id='id' name='id_text' value={formData.id_text} onChange={nameChange} />
                
                <p>입력하신 이름은 {formData.name}이고, 아이디는 {formData.id_text}입니다.</p>
                <input type='button' value='다시쓰기' onClick={onDelete} />
            </form>
        </>
    );
}

export default Form4;