import React from 'react';
import data from './data/customer.json';

function Customer(props) {
    return (
        <>
            <h4>Cutomer json데이터 출력하기</h4>
            <ul>
                {data.map(customer=>(
                    <li key={customer.c_name}>
                        Name : {customer.c_name}&nbsp;
                        Address : {customer.c_address}&nbsp;
                        Tel : {customer.c_tel}&nbsp;
                    </li>
                ))}
            </ul>
        </>
    );
}

export default Customer;