import React from 'react';
import data from './data/goods.json';

function Goods(props) {
    return (
        <>
            <h4>Goods json데이터 출력하기</h4>
            <ul>
                {data.map(good=>(
                    <li key={good.g_code}>
                        Code : {good.g_code}&nbsp;
                        Name : {good.g_name}&nbsp;
                        Cost : {good.g_cost}&nbsp;
                    </li>
                ))}
            </ul>
        </>
    );
}

export default Goods;