import React, {useState} from 'react';
import Usestate from './Usestate';

function LoginForm(props) {
    const form = {
        padding: '0px 20px',
        width: '500px'
    }

    const input_s = {padding: '5px'}

    const [input, setInput] = useState({
        username: '',
        userid: ''
    });

    const {username, userid} = input;

    const onDataChange = (e) => {
        const {value, name}= e.target;
        setInput({
            ...input, 
            [name]:value
        });
    }

    const onDataReset = (e) => {
        setInput({
            username: '',
            userid: ''
        });
    }

    return (
        <>
            <h3>로그인폼 복습하기</h3>
            <ul>
                <li>여러개의 input요소를 사용시 사용될 속성 name, value설정</li>
                <li>e.target.value</li>
                <li>e.target.name</li>
            </ul>

            <fieldset style={form}>
                <legend>폼 그룹</legend>
                <p>
                    <input type='text' value={userid} name='userid' placeholder='아이디를 입력하세요' style={input_s} onChange={onDataChange}/>
                </p>
                <p>
                    <input type='text' value={username} name='username' placeholder='이름을 입력하세요' style={input_s} onChange={onDataChange}/>
                </p>
                <input type='button' value='데이터 초기화' style={input} onClick={onDataReset}/>

                <p>값 출력하기 :{username}:({userid})</p>
            </fieldset>

            <h3>보조설명</h3>
            <pre>
                const [input, setInputs] = useState;
                //값이 1개일 경우 useState()함수에 값을 그냥 적으면 기본값이 들어간다<br />
                //하지만 상태값을 관리하기 위한 값이 2개이기 때문에 2개를 관리하기 위해 따로 설정함<br />
                //그럴 땐 useState()함수에 객체로 입력하면 된다
            </pre>
            <p>객체의 속성으로 값을 호출하는 방법은 아래와 같다<br />
                //input.username; <br />
                //input.userid; <br />
            </p>
            <p>
                //객체 비구조화 할당 방법으로 처리하기<br />
                const username, userid = input;
            </p>
            <p>
                //할당 처리후 호출하는 방법 <br />
                username; <br />
                userid; <br />
                //이렇게 작성하면 표현식으로 호출시 변수명으로 적으면 되니 간단해짐
            </p>
            <p>
                const 중괄호 value, name 중괄호 = e.target; <br />
                //위 수식은 e.target에서 name, value를 추출하여 각각의 변수에 담는다 = 비구조화 할당방식
                <br /><br />
                setInput(중괄호<br />
                ...input, <br />
                [name]:value<br />
                중괄호)
                <br /><br />
                //기존 input값을 복사해 오라는 의미로 나머지 패턴 ...을 사용한다 <br />이 때 객체의 모든 내용을 모두 펼쳐서 복사해 준다
            </p>
            <p>
                //[name]:value에서 대괄호를 쓴 것은 안쓰고 name:value로 쓰면 기존 상태는 제거된다 <br />객체 안에서 key[]로 감싸면 그 안에 넣은 속성명이 가리키는 실제값을 key값으로 사용된다
                <br /><br />
                //객체에 값을 할당할 때 동적인 키값으로 값을 할당할 때 대괄호를 사용한다
            </p>
        </>
    );
}

export default LoginForm;