import React from 'react';

function StudentInput({name, age, email, onCreate, onDataChange}) {
    const input_style = {
        height: '40px',
        margin: '3px'
    }
    const flex = {display: 'flex'}
    const button = {padding: '5px 30px', lineHeight: '40px'}

    return (
        <div style={flex}>
            <input style={input_style} type='text' name='name' placeholder='이름을 입력하세요' value={name} onChange={onDataChange}/>
            <input style={input_style} type='text' name='age' placeholder='나이를 입력하세요' value={age} onChange={onDataChange}/>
            <input style={input_style} type='text' name='email' placeholder='이메일을 입력세요.' value={email} onChange={onDataChange}/>
            <button style={button} type='button' onClick={onCreate}>데이터 추가</button>
        </div>
    );
}

export default StudentInput;